import React from 'react';
import {Avatar, Button, Col, Layout, Menu, Row} from "antd";
import {UserOutlined} from "@ant-design/icons";
import {useDispatch, useSelector} from "react-redux";
import {selectCurrentUserLogin, selectIsAuth} from "../../redux/auth-selector";
import {logout} from "../../redux/auth-reducer";
import {Link} from 'react-router-dom';

export type MapPropsType = {}
export const Header: React.FC<MapPropsType> = (props) => {
    const isAuth = useSelector(selectIsAuth)
    const login = useSelector(selectCurrentUserLogin)

    const dispatch = useDispatch()
    const logoutCallback = () => {
        dispatch(logout())
    }

    const {Header} = Layout
    return <Header className="header">
        <Row>
            <Col span={18}>
                <Menu theme="dark" mode="horizontal"></Menu>
            </Col>
            {isAuth ? <>
                    <Col span={1}>
                        <Avatar style={{backgroundColor: '#87d068'}} icon={<UserOutlined/>}/>
                    </Col>
                    <Col span={5}>
                        <>
                            <div style={{color: "white"}}> {login} <Button style={{marginLeft: 15}} onClick={logoutCallback}>Log out</Button></div>
                        </>
                    </Col>
                </>
                : <Col span={6}>
                    <Button>
                        <Link to={'/login'}>Login</Link>
                    </Button>
                </Col>
            }

        </Row>
    </Header>


    /*<header className={s.header}>
        <img
            src='data:image/png;base64,'/>
        <div className={s.loginBlock}>
            {props.isAuth ? <div> {props.login} - <button onClick={props.logout}>Log out</button></div>
                : <NavLink to={'/login'}>Login</NavLink>}
        </div>
    </header>*/
}
